import React, { Component } from "react";

class Program extends Component {
	
	componentDidMount() {
		this.props.toggleAnimations();
	}

	render() {
		return (
			<div id="program-section">
				<h2><span className="anim-fadein-heading">Program</span></h2>
				<div className="card-container anim-fadein-scaleout">
					<h3 className="card-title">Inwestycje</h3>
					<p className="card-paragraph"> <span class="card-special">remont Placu Dworcowego w Chyloni</span> w celu utworzenia nowoczesnego wza komunikacyjnego</p>
					<p className="card-paragraph"> <span class="card-special">bezkolizyjny przejazd przez tory midzy ul. Chylosk, a ul. Hutnicz</span></p>
					<p className="card-paragraph"> analiza moliwoci alternatywnego poczenia drogowego dla Pustek Cisowskich</p>
				</div>
				<div className="card-container anim-fadein-scaleout">
					<h3 className="card-title">Miasto obywatelskie</h3>
					<p className="card-paragraph"> wikszy udzia mieszkacw w zarzdzaniu miastem (zwikszenie budetw Rad Dzielnic i <span class="card-special"> Budetu Obywatelskiego</span>)</p>
					<p className="card-paragraph"> <span class="card-special">obnika lokalnych podatkw</span> (opaty targowej i podatku od nieruchomoci)</p>
					<p className="card-paragraph"> racjonalne wydawanie publicznych pienidzy</p>
				</div>
				<div className="card-container anim-fadein-scaleout">
					<h3 className="card-title">Dialog</h3>
					<p className="card-paragraph"> konsultacje spoeczne prowadzone przez miasto m.in. w oparciu o gosowanie elektroniczne (np. na stronie BO)</p>
					<p className="card-paragraph"> wizje lokalne z mieszkacami w celu omwienia lokalnych potrzeb</p>
					<p className="card-paragraph"> wsuchiwanie si w gos mieszkacw odnonie decyzji podejmowanych przez miasto</p>
				</div>
				<div className="card-container anim-fadein-scaleout">
					<h3 className="card-title">Dzielnice</h3>
					<p className="card-paragraph"> wicej inwestycji na Chyloni, Cisowej, Pustkach i Demptowie (koniec z odwlekaniem projektw i pomijaniem naszych dzielnic w planie inwestycyjnym miasta)</p>
					<p className="card-paragraph"> kontrolowanie terminowoci inwestycji (m.in. projektw Budetu Obywatelskiego oraz zada inwestycyjnych rad dzielnic) w celu uniknicia opnie</p>
				</div>
				<div className="card-container anim-fadein-scaleout">
					<h3 className="card-title">Potrzeby mieszkacw</h3>
					<p className="card-paragraph"> oglna poprawa stanu drg i chodnikw</p>
					<p className="card-paragraph"> wiksza dostpno miejsc w publicznych obkach</p>
					<p className="card-paragraph"> nowoczesne, kreatywne i nieszablonowe place zabaw</p>
					<p className="card-paragraph"> dbao o miejsca wypoczynku i rekreacji w dzielnicach</p>
				</div>
				<div className="card-container anim-fadein-scaleout">
					<h3 className="card-title">Przyjazna przestrze</h3>
					<p className="card-paragraph"> <span class="card-special">zagospodarowanie terenu byego domu dziecka na Demptowie</span> jako funkcjonalnej i oglnodostpnej przestrzeni</p>
					<p className="card-paragraph"> utworzenie osiedlowych stref zieleni i <span class="card-special">parkw kieszonkowych</span></p>
					<p className="card-paragraph"> likwidacja barier architektonicznych dla osb niepenosprawnych</p>
				</div>
				<div className="card-container anim-fadein-scaleout">
					<h3 className="card-title">Komunikacja</h3>
					<p className="card-paragraph"> <span class="card-special">przeduenie linii PKM do Chyloni</span></p>
					<p className="card-paragraph"> lepsza integracja komunikacji miejskiej (np. autobusy z SKM)</p>
					<p className="card-paragraph"> decyzje oparte na wieloaspektowej analizie (zdanie mieszkacw, racjonalno, koszty i wpyw na rodowisko naturalne)</p>
					<p className="card-paragraph"> analiza montau biletomatw</p>
					<p className="card-paragraph"> zwikszenie liczby parkingw</p>
				</div>
				<div className="card-container anim-fadein-scaleout">
					<h3 className="card-title">Przejrzysto dziaa</h3>
					<p className="card-paragraph"> ranking przyszych inwestycji, aby mieszkacy i rady dzielnic wiedzieli jakich przedsiwzi si spodziewa</p>
					<p className="card-paragraph"> konsultowanie z mieszkacami tematw obrad Rady Miasta</p>
					<p className="card-paragraph"> uproszczona wersja budetu miasta przystpnie prezentujca wydatki miasta</p>
				</div>
				<div className="card-container anim-fadein-scaleout">
					<h3 className="card-title">Wsppraca</h3>
					<p className="card-paragraph"> sprawniejsza koordynacja remontw w dzielnicach</p>
					<p className="card-paragraph"> popieranie wszystkich dobrych pomysw bez wzgldu na autorstwo</p>
					<p className="card-paragraph"> uczestnictwo w sesjach rad dzielnic</p>
					<p className="card-paragraph"> wymiana dobrych praktyk i wzajemnych inspiracji z samorzdowcami z innych miast</p>
				</div>
			</div>
		);
	}
}

export default Program;


