import React, { Component } from "react";

class Wizja extends Component {
	
	componentDidMount() {
		this.props.toggleAnimations();
	}
	
	render() {
		return (
			<div id="wizja-section">
				<h2><span className="anim-fadein-heading">Wizja</span></h2>
				<div className="text-container">
					<div className="para_header anim-fadein-scaleout">
						Jako Radny bd podejmowa dialog z mieszkacami, aby jak najlepiej pozna ich oczekiwania i problemy. Chc wykorzysta moj modo i energi na prac na rzecz Gdyni. Bd aktywnie i z pasj realizowa i wspiera projekty dotyczce naszego miasta.
					</div>
					<div className="para_align-left anim-slideleft">
						<strong>Moja Gdynia</strong> to miasto otwarte, obywatelskie i dziaajce na przejrzystych zasadach.
					</div>
					<div className="para_align-right anim-slideright">
						<strong>Moja Gdynia</strong> to miasto, ktre uczciwie przeprowadza konsultacje spoeczne. Mieszkacy powinni by pytani o zdanie w kwestiach wraliwych i istotnych dla lokalnej spoecznoci.
					</div>
					<div className="para_align-left anim-slideleft">
						<strong>Moja Gdynia</strong> to miasto, ktre dba o wszystkie swoje dzielnice i swoj uwag powica nie tylko projektom oglnomiejskim, ale take sucym konkretnym dzielnicom.
					</div>
					<div className="para_align-right anim-slideright">
						<strong>Moja Gdynia</strong> korzysta z zaangaowania osb pracujcych na jej rzecz, nie zwaajc na partyjne i inne nieistotne podziay.
					</div>
				</div>
			</div>
		);
	}
}

export default Wizja;


