import React, { Component } from "react";

import aboutme from "../assets/aboutme.jpg"

class O_mnie extends Component {
	
	componentDidMount() {
		this.props.toggleAnimations();
	}

	render() {
		return (
			<div id="o_mnie-section">
				<h2><span className="anim-fadein-heading">O mnie</span></h2>
				<div className="text-container">
					<img src={aboutme} alt="aboutme.jpg" className="anim-fadein-text"/>
					<p className="anim-fadein-text">
						Jestem modym, ambitnym mieszkacem Gdyni. Mieszkam na Pustkach Cisowskich praktycznie od zawsze. To moje miejsce na ziemi i chciabym, by zmieniao si na lepsze zgodnie z oczekiwaniami mieszkacw. Potrafi o te zmiany walczy i do nich przekonywa. Mimo modego wieku, mam due dowiadczenie w pracy spoecznej. Od dawna pomagaem osobom w moim otoczeniu. Byem przewodniczcym komisji w Modzieowej Radzie Miasta, przewodniczcym samorzdu szkolnego, a od 4 lat jestem radnym dzielnicy Pustki Cisowskie-Demptowo.
					</p>
					<p className="anim-fadein-text">
						Umiem dobrze organizowa prac swoj i zespow, bez problemu radziem sobie z czeniem pracy zawodowej i studiw dziennych. Lubi podejmowa nowe wyzwania i traktuj je powanie.
					</p>
					<p className="anim-fadein-text">
						Jestem absolwentem automatyki i robotyki na Politechnice Gdaskiej, nagrodzonym Zot Odznak Absolwenta. Obecnie studiuj na ostatnim semestrze studiw licencjackich z ekonomii na specjalizacji Polityka Gospodarcza i Strategia Przedsibiorczoci, a take pracuj w duej firmie informatycznej. W sierpniu rozpoczem nowy etap w moim yciu, biorc lub.
					</p>
					<p className="anim-fadein-text">
						Na Pustkach Cisowskich spdziem cae swoje ycie. Dostrzegam potrzeby Chyloni, Cisowej, Pustek oraz Demptowa i chc wykorzysta moja modo i energi do pracy na rzecz tych dzielnic i caej Gdyni.
					</p>
					<p className="anim-fadein-text">
						W Gdyni yje si dobrze  to zasuga przede wszystkim mieszkacw. Zatem powinni oni mie realny wpyw na rozwj miasta. Ceni dialog, lubi sucha mieszkacw i pracowa dla nich  jestem gotowy podejmowa kolejne wyzwania, by zmienia nasze miasto i dzielnice. Wierz, e zmiana jest moliwa. Potrzebna jest nowa energia i lepsze wykorzystanie potencjau miasta i ludzi.
					</p>
				</div>
			</div>
		);
	}
}

export default O_mnie;


