import React, { Component } from "react";

// photos:
import ad from "../assets/rekomendacje/ad.jpg"
import tp from "../assets/rekomendacje/tp.jpg"
import pb from "../assets/rekomendacje/pb.jpg"
import pg from "../assets/rekomendacje/pg.jpg"
import zzt from "../assets/rekomendacje/zzt.jpg"

class Rekomendacje extends Component {
	
	componentDidMount() {
		this.props.toggleAnimations();
	}

	render() {
		return (
			<div id="rekomendacje-section">
				<h2><span className="anim-fadein-heading">Rekomendacje</span></h2>
				<div className="content-container">
					<div className="person-wrapper">
						<img src={zzt} alt="Zygmunt Zmuda Trzebiatowski JPG" className="anim-fadein-scaleout"/>
						<h4 className="person-name anim-fadein-scaleout">Zygmunt Zmuda Trzebiatowski</h4>
						<span className="person-whois anim-fadein-text">wiceprzewodniczcy Rady Miasta Gdyni</span>
						<p className="description-wrapper anim-fadein-text">
							Bartek to umiechnity, pogodny i zaangaowany mody czowiek, ktrego kady chciaby mie w swoim zespole. yje problemami swojej dzielnicy i skutecznie je rozwizuje. Po 4 latach pracy dla swojej dzielnicy jest gotw na wicej, a ja bardzo licz, e dostanie tak moliwo. Znam od lat, obserwuj w dziaaniu, popieram, trzymam kciuki i zachcam do gosowania na Bartka. Takich ludzi potrzebuje gdyska rada miasta!
						</p>
					</div>
					<div className="person-wrapper">
						<img src={ad} alt="Artur Dziambor JPG" className="anim-fadein-scaleout"/>
						<h4 className="person-name anim-fadein-scaleout">Artur Dziambor</h4>
						<span className="person-whois anim-fadein-text">radny dzielnicy Cisowa</span>
						<p className="description-wrapper anim-fadein-text">
							Czasem spotyka si takich ludzi, z ktrymi wystarczy zamieni kilka zda, aby doj do wniosku, e chciaoby si z nimi wsppracowa. Bartka poznaem wiele lat temu, gdy by jeszcze bardzo mody, a ju wyjtkowo aktywny. Od samego pocztku bardzo mu zaleao aby wprowadza wielkie pozytywne zmiany i aby by kreatorem otaczajcej nas rzeczywistoci. Jestem przekonany, e dowiadczenie zdobyte przez lata pracy na rzecz jego dzielnicy zostanie doskonale wykorzystane na rzecz wszystkich mieszkacw Gdyni. Przed Bartkiem kampania wyborcza. Koleanek i kolegw przekonywa nie musia, swojego kandydata maj od momentu gdy ogosi start. Trzymam kciuki za to aby Gdynianie, ktrzy nie mieli okazji pozna Bartka osobicie, obdarzyli go podobnym zaufaniem.
						</p>
					</div>
					<div className="person-wrapper">
						<img src={pg} alt="Przemysaw Grenda JPG" className="anim-fadein-scaleout"/>
						<h4 className="person-name anim-fadein-scaleout">Przemysaw Grenda</h4>
						<span className="person-whois anim-fadein-text">radny dzielnicy Chylonia</span>
						<p className="description-wrapper anim-fadein-text">
							Ch pracy na rzecz lokalnej spoecznoci to cecha, ktra w rodzinie Bartka jest gboko zakorzeniona, doskonale wiedz o tym mieszkacy jego rodzimej dzielnicy Pustki Cisowskie-Demptowo, do ktrej rozwoju od 4 lat przyczynia si w ramach dziaa Rady Dzielnicy. Jego hobbistyczne zacicie zwizane z polityk i ekonomi sigajce lat szkolnych przerodzio si w studia w tym kierunku oraz ostatecznie w bardzo dobrze ugruntowan wiedz, ktra wraz z analitycznym i penym pomysw umysem przyczyni si  jestem pewien, do pracy w Radzie Miasta na najwyszym moliwym poziomie.
						</p>
					</div>
					<div className="person-wrapper">
						<img src={pb} alt="Pawe Borkowski JPG" className="anim-fadein-scaleout"/>
						<h4 className="person-name anim-fadein-scaleout">Pawe Borkowski</h4>
						<span className="person-whois anim-fadein-text">redaktor naczelny Jestem z Gdyni</span>
						<p className="description-wrapper anim-fadein-text">
							Lewandowski jest pikarzem od ktrego kady trener zaczby ustalanie swojego skadu. Podobnie jest z Bartkiem. yczybym kademu, kto zaczyna dowolny projekt spoeczno-polityczny aby mia moliwo otwiera swj zesp tak osob. Profesjonalizm idzie tutaj w parze z zaangaowaniem... i umiechem ktrym zaraa i motywuje innych. Bdc mieszkacem okrgu nr 2 nie miabym wtpliwoci - Bartek to pewno dobrego wyboru.
						</p>
					</div>
					<div className="person-wrapper">
						<img src={tp} alt="Tomasz Prabucki JPG" className="anim-fadein-scaleout"/>
						<h4 className="person-name anim-fadein-scaleout">Tomasz Prabucki</h4>
						<span className="person-whois anim-fadein-text">przewodniczcy Modzieowej Rady Miasta Gdyni III kadencji</span>
						<p className="description-wrapper anim-fadein-text">
							Bartka znam ju od ponad 10 lat. Od kiedy pamitam wspiera osoby w swoim otoczeniu i stara si dziaa dla dobra lokalnej spoecznoci. Robi to solidnie i z godnym podziwu zaangaowaniem. Mam nadziej, e w Radzie Miasta Gdyni znajd si osoby wanie takie jak Bartek. Gdynia tego potrzebuje :).
						</p>
					</div>
				</div>
			</div>
		);
	}
}

export default Rekomendacje;


