import React, { Component } from "react";

class Home extends Component {
	
	componentDidMount() {
		this.props.toggleAnimations();
	}

	render() {
		return (
			<div id="home-section">
				<h2><span className="anim-fadein-heading">Strona gwna</span></h2>
				<div className="text-container">
					<p className="home-para-heading anim-fadein-scalein">
						Witaj na mojej stronie
					</p>
					<p className="home-para anim-fadein-text ">
						Biorc pod uwag ile materiaw wyborczych przewija si w trakcie kampanii wyborczej <span className="home-special">jestem zaszczycony, e tutaj jeste</span>.
					</p>
					<p className="home-para anim-fadein-text ">
						Zapraszam do poczytania o mnie i moich postulatach.
					</p>
					<p className="home-para anim-fadein-text ">
						Zachcam rwnie do rozmw o Gdyni i jej dzielnicach.
					</p>
				</div>
			</div>
		);
	}
}

export default Home;


